.landing-section-a{
	display: flex;
	flex: 1 1 60%;
	overflow-y: auto;
	position: relative;
}
.landing-section-a-small{
	display: flex;
	flex: 1 1 40%;
	position: relative;
}
@media screen and (max-width: 1200px){
   .landing-section-a{
   		flex: 1 1 100%;
   		height: 100%
   }

}
@media screen and (max-width:1024px) {
  	.landing-section-a{
 		height: initial;
 	}
 	.landing-section-a,
 	.landing-section-a-small{
 		min-height: 612px;
 	}
} 
@media screen and (max-width:800px) {
  	.landing-responsive{
 		flex: 1 1 100%;
 	} 
}

@media screen and (max-width: 567px){
  	.landing-section-a,
  	.landing-section-a-small{
 		display: flex;
 		flex-direction: column;
 	}



}