.section-menu{
	margin: 35px 0px 0px 35px;
	position: fixed;
}
.item-menu{
	font-size: 30px;
}
.sub-item-menu{
	font-size: 1.5em;
	margin-left: 24px;
}
.menu-options{
	margin: 10px 0px 0px 10px;
}
.active-yellow{
	border-bottom: 1px solid #ac9928
}
.active-purple{
	border-bottom: 1px solid #6e2b40
}
.active-white{
	border-bottom: 1px solid #ffffff
}


@media screen and (max-width: 1200px){
	.section-menu{
		position: initial;
	}
}
@media screen and (max-width:1024px) {
  
} 
@media screen and (max-width:800px) {
 	.section-menu{
		margin-right: 20px;
	}
  
}     

@media screen and (max-width: 567px){
	.item-menu,
	.sub-item-menu{
		font-size: 1.25em;
	}
	.section-menu{
		max-width: 170px;

	}

}