*{
	margin: 0;
	padding: 0;
}

.color-white{
	color: #fff
}
.color-purple-5{
	color: #6e2b40 
}
.bg-black-5{
	background: #211915
}
.bg-purple-5{
	background: #6e2b40 
}
.bg-yellow-5{
	background: #ac9928
}

.row-position{
	display: flex;
	flex-direction: row;
}
.column{
	display: flex;
	flex-direction: column;
}
.align-center{
	display: flex;
	align-items: center;
}
.justify-center{
	display: flex;
	justify-content: center;
}
.justify-end{
	display: flex;
	justify-content: flex-end;
}
.text-align-right{
	text-align: right;
}
.cursor{
	cursor: pointer;
}
a{
	text-decoration: none;
}



h1{
	font-size: 2.1875em;
}
h2{
	font-size: 1.5em;
}
h3{
	font-size: 1.125em;
}
p{
	font-size: 0.875em;
}
h4{
	font-size: 0.875em;
}

.font-medium{
	font-weight: 500;
}

.section-landing{
	height: 100vh;
}

@media screen and (max-width: 1200px){
    .section-landing{
    	display: flex;
    	flex-flow: row wrap
    }
}
